import {Router, ActivatedRoute} from '@angular/router';
import {Component, OnInit} from '@angular/core';
import {DelegateService} from '../../services/delegate/delegate.service';
import swal from 'sweetalert2';

@Component({
    selector: 'app-edit-delegates',
    templateUrl: './edit-delegates.component.html',
    styleUrls: ['./edit-delegates.component.css']
})
export class EditDelegatesComponent implements OnInit {
    delegate = {
        _id: '',
        name: '',
        lastName: '',
        email: '',
        birth: '',
        idType: '',
        idNumber: '',
        cellphone: '',
        country: '',
        city: '',
        institution: '',
        degree: '',
        blood: '',
        insurance: '',
        specialCondition: '',
        contactName: '',
        contactPhone: '',
        numberMun: '',
        committee1: '',
        committee2: '',
        committee3: '',
        finalCommittee: '',
        code: ''
    };
    delegateToFind = {
        id: ''
    };


    constructor(private delegateService: DelegateService, private router: Router, private activatedRoute: ActivatedRoute) {
        this.getStaffParams();
        this.findDelegate();

    }

    ngOnInit() {
    }

    getStaffParams() {
        this.activatedRoute.params.subscribe(params => {
            this.delegateToFind.id = params['id'];
        });
    }

    findDelegate() {
        this.delegateService.findOneDelegate(this.delegateToFind)
            .subscribe(
                staffRes => {
                    this.delegate = staffRes;
                },
                err => {
                    swal({
                        type: 'error',
                        title: 'Oops...',
                        text: err.json().message,
                        confirmButtonColor: '#d33',
                    });
                });
    }

    committees(option) {
        switch (option) {
            case '1':
                return 'Consejo de Seguridad';
            case '2':
                return 'Comité de Estado Mayor';
            case '3':
                return 'Human Rights Council';
            case '4':
                return 'Comité del Patrimonio de la Humanidad';
            case '5':
                return 'Comisión de Derecho Internacional';
            case '6':
                return 'Senado de la República';
            case '7':
                return 'Mesa de Astaná';
            case '8':
                return 'Consorcio Mundial de Prensa';
            case '9':
                return 'Juego de Tronos';
            case '10':
                return 'GAC Electoral';
            default:
                console.log('Contacte con prensa.munum@gmail.com');
        }
    }

    onSubmit() {
        console.log(this.delegate);
        this.delegateService.updateDelegate(this.delegate)
            .subscribe(
                delegate => {
                    this.router.navigate(['admin/delegates']);
                },
                err => {
                    swal({
                        type: 'error',
                        title: 'Oops...',
                        text: err.json().message,
                        confirmButtonColor: '#d33',
                    });
                });
    }

}



